@import "./assets/styles/theme";

body {
  margin: 0px;
  position: relative;
  overflow-x: hidden;
  font-family: 'BYekan', Calibri;
}

%my-button{
  height: 40px;
  width: 100%;
  border-radius: 5px;
  box-shadow: 3px 3px 6px 0px rgb(145,145,145);
}

.default-button-shadow {
  box-shadow: 3px 3px 6px 0px rgb(145,145,145);
}

.bold {
}

button.no-style {
  background-color: inherit;
  border: none;
}
.primary-button {
  background-image: linear-gradient(to top, $primaryColor, lighten($primaryColor, 20));
  border: none;
  border-radius: 3px;
  @extend .default-button-shadow;
}

.primary-button:hover {
  background-image: linear-gradient(to top, $primaryColor, darken($primaryColor, 10));
  border-color: darken($color: $primaryColor, $amount: 15);
}
.secondary-button {
  background-color: $primaryGray;
  border-color: $primaryGray;
  @extend .default-button-shadow;
}

.secondary-button:hover {
  background-color: darken($color: $primaryGray, $amount: 5);
  border-color: darken($color: $primaryGray, $amount: 5);
}