
@import '../../assets/styles/theme';
.placeholder {
    background-color: $darkLight;
    height: 4px;
    width: 100%;
}

.progress-bar {
    position: absolute;
    .MuiLinearProgress-colorPrimary {
        background-color: $darkLight;
    }
    .MuiLinearProgress-barColorPrimary {
        background-image: linear-gradient(to top, $gd, lighten($gd, 10));
    }
}

