
#ParrotControl {
  width: 100%;
  height: 100%;
  overflow-y: hidden;

  .parrot-area {
    overflow-y: auto;
  }

  .parrot-area.red-parrot {
    background-color: #feae96;
    background-image: linear-gradient(315deg, #feae96 0%, #fe0944 74%);
  }

  .parrot-area.blue-parrot {
    background-color: #2a2a72;
    background-image: linear-gradient(315deg, #2a2a72 0%, #009ffd 74%);
  }
}
.Temp {
  border: 3px solid black;
}
.ParrotControlBox {
  width: 100%;
  direction: rtl;
  text-align: center;
  border: 1px solid #D6D6D6;
  position: relative;
  width: 80%;
  background-color: #ffffff;
  background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%);  //border: 2px solid #2a2a72;
  height: 100%;
  overflow-y: hidden;
  .command-section {
    overflow-y: hidden;
    //overflow-y: auto;

  }

  .command-section.movement {
    height: 40%;
  }

  .command-section.voice {
    height: 60%;
  }

  .command-box {
    $commandBtnSize: 70px;
    $fontSize: $commandBtnSize * 1/5;
    height: 100%;
    overflow-y: auto;
    align-content: flex-start;

    .category-buttons {
      border-bottom: 1px solid lightgray;
    }

    .button-surrounding {
      padding: 0.2em;

      font-family: "BYekan", "Arial";
      button {
        padding: 0.2em 0.8em;
        height: 3em;
        max-width: 6em;
        font-size: 0.9em;
        overflow: hidden;
        white-space: normal;
        display: inline-block;
        text-overflow: ellipsis;
        // background-color: red;
      }

      
    }

  }


  .temp.has-border {
    flex-basis: 100%;
    @media (max-width: 768px) {
      //border-bottom: 1px solid #D6D6D6;
    }
    @media (min-width: 768px) {
      border-left: 1px solid #D6D6D6;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
}

.ParrotControlBox.disabled {
  button {
    pointer-events: none;
  }

  .command-box {
    .button-surrounding {
      .command-btn {
      }
    }
  }
}